body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  user-select: none;
  /* background: linear-gradient(to bottom, rgba(19, 7, 47, 0.998), #000000, 100%); */
  background: linear-gradient(to bottom, rgba(19, 7, 47, 0.998), #000000 60%, #000000 100%);
}

