.header-cont {
  height: 10vh;
  /* display: flex;
  justify-content: end;
  align-items: center; */
  padding: 0rem 1rem;
}

.dropdown-cont {
  border-radius: 0.3125rem;
  background: transparent;
  color: #fff;
  padding: 0.25rem 1.75rem 0.25rem 0.65rem;
  position: absolute;
  width: fit-content;
  right: 1rem;
  margin-top: 2rem;
  z-index: 115;
  border: 0.05rem solid #3f3f3f;
  cursor: pointer;
}

.lang-option {
  /* padding: 0.25rem 1.75rem 0.25rem 0.65rem; */
  width: 100%;
  font-size: 0.9rem;
}

.dropdown-cont.active {
  /* styles for when the dropdown is open */
  padding: 0 0 0.1rem 0 !important;
}

.dropdown-cont.active ul {
  padding: 0.5rem 0.5rem;
  margin: 0;
  list-style: none;
}

.dropdown-cont.active ul li {
  padding: 0;
  margin: 0;
  list-style: none;
  width: 2.5rem;
  padding-left: 0.5rem;
  padding-top: 0.1rem;
  letter-spacing: 0.075rem;
  cursor: pointer;
}

.dropdown-cont.active ul li:nth-child(2) {
  padding: 0;
  margin: 0;
  list-style: none;
  width: 2.5rem;
  margin-top: 0.3rem;
  padding-left: 0.5rem;
  letter-spacing: 0.075rem;
  font-weight: 900;
  padding-top: 0.2rem;
}

.dropdown-cont.active ul li:hover {
  background: #391f67;
}

.dropdown-image {
  width: 0.5rem;
  position: absolute;
  right: 15%;
  top: 55%;
  transform: translateY(-50%);
  -webkit-transform: translateY(-50%);
  -moz-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  -o-transform: translateY(-50%);
}

.selected-lang {
  font-size: 0.8rem;
  letter-spacing: 0.075rem;
}

.bot-cont {
  height: 50vh;
}

.bottom-container {
  position: relative;
  display: flex;
}

.dummy-container {
  width: 20%;
  height: 100%;
}

.response-cont {
  overflow-y: scroll;
  position: relative;
  z-index: 100;
  /* height: 22.5vh; */
  -webkit-mask-image: linear-gradient(to bottom, black 60%, transparent);
  mask-image: linear-gradient(to bottom, black 60%, transparent);
  width: 80%;
  height: 85%;
}

.response-cont.centered {
  display: flex;
  justify-content: center;
  align-items: center;
}

.recording-cont {
  /* height: 17.5vh; */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  width: 20%;
  height: 100%;
  float: right;
  /* margin-top: 0.75rem; */
}

.gc-response {
  color: white;
  opacity: 0.7;
  word-spacing: 0.08rem;
  letter-spacing: 0.065rem;
  /* font-style: italic; */
  text-align: center;
  padding: 0rem 3rem 2rem 3rem;
  font-size: 0.95rem;
  line-height: 1.3rem;
}

@media (min-width: 768px) {
  .response-cont {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .gc-response {
    padding: 0rem 3rem;
  }
  .header-cont {
    padding: 0rem 3rem;
  }
  .dropdown-cont {
    right: 3.5rem;
  }
}

@media (min-width: 1200px) {
  .gc-response {
    padding: 0rem 4rem;
    margin-top: 1rem;
    font-size: 1rem;
  }
}

@media (min-width: 1400px) {
  .gc-response {
    padding: 0rem 10rem;
    /* margin-top: 1.5rem;/ */
  }
}

@media (min-width: 1600px) {
  .gc-response {
    padding: 0rem 12rem;
    /* margin-top: 2rem; */
  }
}


.ui-rec-btn {
  padding: 1.05rem 1.05rem 0.75rem 1.05rem;
  border-radius: 100%;
  cursor: pointer;
  outline: inherit;
  color: #fff;
  -webkit-border-radius: 100%;
  -moz-border-radius: 100%;
  -ms-border-radius: 100%;
  -o-border-radius: 100%;
  border-radius: 3.125rem;
  background: radial-gradient(218.89% 191.66% at 50.15% 0.00%, rgba(103, 43, 255, 0.75) 0%, #AF6CFC 120%);
  border: 0.3rem solid rgb(49 27 104);
  box-shadow: 0px 10px 70px 3px #3a2376;
  margin-top: -3rem !important;
}

.ui-rec-btn:active {
  background: radial-gradient(218.89% 191.66% at 50.15% 0.00%, rgb(91 46 208 / 90%) 0%, #5f10ba 100%);
}

.ui-rec-btn:active,
.ui-rec-btn:focus {
  outline: none;
  -webkit-tap-highlight-color: transparent;
}

@keyframes beat {
  0% {
    box-shadow: 0px 10px 100px 3px #532DB2;
    border: 0.3rem solid rgb(49 27 104);
  }
  50% {
    box-shadow: 0px 10px 100px 1px #532DB2;
    border: 1rem solid rgb(46, 28, 92);
  }
  100% {
    box-shadow: 0px 10px 100px 3px #532DB2;
    border: 0.3rem solid rgb(49 27 104);
  }
}

.ui-rec-btn.active {
    animation: beat 1.5s infinite;
    -webkit-animation: beat 1.5s infinite;
}

.info-text {
  color: #ffffff;
  margin-top: 0.65rem;
  word-spacing: 0.05rem;
  letter-spacing: 0.02rem;
  font-style: italic;
  font-size: 0.85rem;
  position: absolute;
  margin-top: 3.75rem;
}

.loading-screen {
  width: 100vw;
  height: 100vh;
  position: absolute;
  background: #000000;
  z-index: 130;
}

.load-start-cont {
  position: absolute;
  left: 50%;
  top: 40%;
  transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  -o-transform: translate(-50%, -50%);
}

.loading-bar {
  position: relative;
  /* width: 20rem; */
  width: 12rem;
}

.xrv-logo-img {
  width: 7rem;
}

.start-button {
  padding: 0.75rem 2rem;
  font-size: 1rem;
  border-radius: 2rem;
  border: 4px solid rgba(83, 45, 178, 0.60);
  background: radial-gradient(218.89% 191.66% at 50.15% 0.00%, rgba(99, 51, 221, 0.9) 0%, #AF6CFC 100%);
  box-shadow: 2px 2px 30px 3px #532DB2;
  color: white;
  cursor: pointer;
  -webkit-border-radius: 2rem;
  -moz-border-radius: 2rem;
  -ms-border-radius: 2rem;
  -o-border-radius: 2rem;
}

@media (min-width: 350px) {
  .loading-bar {
    width: 15rem;
  }
  .xrv-logo-img {
    width: 7.5rem;
  }
}

@media (min-width: 768px) {
  .loading-bar {
    width: 18rem;
  }
  .xrv-logo-img {
    width: 9rem;
  }
  .start-button {
    font-size: 1.15rem;
  }
}

.start-button:hover {
  background: radial-gradient(218.89% 191.66% at 50.15% 0.00%, rgba(99, 51, 221, 1) 0%, #AF6CFC 100%);
  box-shadow: 2px 2px 35px 4px #532DB2;
}

.xrv-logo {
  position: absolute;
  left: 50%;
  top: 70%;
  transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  -o-transform: translate(-50%, -50%);
  text-align: center;
}

.xrv-logo-text {
  color: #fff;
  font-size: 0.8rem;
  margin-bottom: 2rem;
  font-style: italic;
}

